@use '../../styles' as s;



.eventImage{
    height: 150px;
    width: inherit;
    border-top-left-radius: s.$radius;
    border-top-right-radius: s.$radius;
}

.eventCard{
    background-color: s.$primary;
    color: s.$text;
    width: 275px;
    height: 400px;
    margin: 18px;
    border-radius: s.$radius;
}


.eventGenre{
    background-color: #9b5b7d05;
    width: fit-content;
    padding: 5px 5px 4px 6px;  //top, right, bottom, left
    border-radius: 2px; 
    font-size: 12px;
}

.music{
    background-color: s.$music;
}

.sports{
    background-color: s.$sports;
}

.artstheatre{
    background-color: s.$artstheatre;
}

.miscellaneous{
    background-color: s.$miscellaneous;
}

.eventInfo{
    margin-left: 7%;
    margin-top: 5%;
    margin-right: 7%;
    div{ //here I am applying margin top to all divs that come after the div of a className eventInfo
        margin-top: 6%;
    }
}

.eventName{
    font-size: 20px;
    font-weight: bolder;
    font-weight: 700;
}

.eventDate{
    color: #e2c649;
    font-weight: 550;
}

.eventLocation{
    display: flex;
    flex-direction: row;
    align-items: center;
    img{
        height: 21px;
        filter: grayscale(100%);
        margin-right: 1.5%;
    }
    div {
        margin-bottom: 5%;
    }
}



.eventButton{
    cursor: pointer;
    border-radius: 2px;
    border: none;
    font-size: 18px;
    &:hover{
        cursor: pointer;
        font-size: 16px;
    }
    &:active{
        font-size: 14px;
    }
}

.eventButtonContainer{
    display: flex;
    justify-content: space-between;
}

.eventButtonInfo{
    cursor: pointer;
    font-size: 18px;
    margin-left: 3%;
    margin-bottom: 3%;

}

.eventButtonHeart{
    cursor: pointer;
    height: 25px;
    width: 25px;
    margin-right: 8%;
    &.unfavorited{
        filter: invert(80%);
    }
}